.dropdown {
    cursor: pointer;
    color: black;
    width: 400px;
    font-size: 20px;
    position: relative;
    box-shadow: 0px 0px 9px 2px rgba(0, 0, 0, 0.5);
    .dropdownBtn {
        padding: 15px;
        background-color: white;
        border-radius: 10px;
    }
    .dropdownBtn.active {
        border-radius: 10px 10px 0 0;
    }
    .dropdownContent {
        position: absolute;
        z-index: 10;
        background-color: white;
        width: 100%;
        box-shadow: 0px 14px 25px -5px rgba(0,0,0,0.45);
        border-radius: 0 0 10px 10px ;
    }
    .dropdownItem {
        padding: 15px;
        border-top: 1px solid rgb(202, 202, 202);
    }
}