.modal {
    background-color: rgba(0, 0, 0, 0.318);
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    button {
        font-size: 25px;
        padding: 10px 20px;
        outline: 0;
        border: 0;
        cursor: pointer;
        color: white;
        background-color: #00897b;
        border-radius: 10px;
        transition: 0.3s;
        &:hover {
            background-color: #00534a;
        }
    }
}

.boxImg {
    max-width: 1140px;
    max-height: 800px;
    margin: 0 30px;
    img {
        width: 100%;
        max-height: 800px;
    }
}