.container {
    text-align: center;

    .title {
        font-size: 60px;
        margin-bottom: 15px;
        text-transform: uppercase;
    }
    .subtitle {
        font-size: 30px;
        color: rgb(143, 143, 143);
        margin-bottom: 40px;
    }
}

.filmsWrapper {
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.3);
}