header {
    padding: 40px 0;
    box-shadow: 0px 9px 8px -5px rgba(0, 0, 0, 0.15);
}

.container {
    display: flex;
    text-align: center;
    justify-content: space-between;

    ul {
        display: flex;
        text-align: center;

        li {
            font-size: 20px;
            margin-left: 50px;
        }
    }

    .logo {
        color: #00897b;
        font-weight: 700;
        font-size: 30px;
        transition: 0.3s;
        text-transform: uppercase;
        &:hover {
            color: #c9c9c9;
        }
    }
}