.filmWrapper {
    background-color: white;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.3);
    display: flex;
    .wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        button {
            padding: 10px 10px;
            font-size: 15px;
            border-radius: 5px;
            background-color: #00897b;
            color: white;
            cursor: pointer;
            border: 0;
            transition: 0.3s;
            &:hover {
                background-color: #00564d;
            }
        }
    }
}

.imgBlock {
    margin-right: 30px;
    img {
        border-radius: 10px;
    }
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h2 {
        color: #363636;
    }
    p {
        color: #363636;
        font-size: 20px;
        span {
            font-weight: 700;
        }
    }

}

