.filmsWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.container {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.3);
    padding: 50px;
    h2 {
        margin-bottom: 20px;
        font-size: 30px;
        color: #303030;
    }
    h4 {
        margin-bottom: 40px;
        color: #303030;
        font-size: 20px;
    }
}

.input {
    font-size: 30px;
    margin-bottom: 20px;
    outline: 0;
    width: 100%;
    border: 1px solid gray;
    padding: 10px;
    border-radius: 10px;
}

.notfound {
    font-size: 30px;
}