$dark: #303030;
$mediumDark: #363636;
$grayDark: #969696;
$lightGreen: #00897b;
$darkGreen: #00564d;
$textColor: #c9c9c9;

* {
    box-sizing: border-box;
}

body {
    background-color: $dark;
    font-weight: 400;
}

a {
    text-decoration: none;
    color: $textColor;
    transition: 0.3s;
    &:hover {
        color: $lightGreen;
    }
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    color: $textColor;
}

ul, li {
    display: block;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1140px;
    margin: 0 auto;
}

.active {
    color: $lightGreen;
}

section {
    padding: 60px 0;
}

img {
    vertical-align: top;
}

.filmLink {
    padding: 10px;
    &:hover {
        border-radius: 10px;
        box-shadow: 0px 0px 9px 2px rgba(0, 0, 0, 0.4);
    }
}

::-webkit-scrollbar {
    width: 10px;
    height: 4px;
    background-color: #e2e2e2;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #00897b94;
}