.container {
    h3 {
        font-size: 30px;
        span {
            color: #00897b;
        }
    }
}

.filmsWrapper {
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.3);
}

.selectCategory {
    display: flex;
    align-items: center;
    h3 {
        margin-right: 20px;
    }
    margin-bottom: 40px;
}