.mainFooter {
    box-shadow: 0px -6px 11px 0px rgba(0, 0, 0, 0.2);
    .container {
        padding: 45px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    a {
        text-decoration: none;
        color: #d3d3d3;
        font-size: 25px;
        transition: 0.3s;
        &:hover {
            color: #00897b;
        }
    }
    span {
        color: #d3d3d3;
    }
    p {
        font-size: 12px;
    }
    .contactFooter {
        text-align: end;
    }
}